/* components/styles/Contact.css */
.contact-section {
  width: 100%;
  padding: 60px 10%;
  background: linear-gradient(to right, #1e5b5e, #313a4b); /* Gradient ƒë·∫πp */
  text-align: center;
  box-sizing: border-box;
  min-height: calc(100vh - 70px);
}

.contact-section h2 {
  font-size: 2.5rem;
  color: #ffffff;
  margin-bottom: 40px;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.contact-info {
