  margin-bottom: 40px;
}

.contact-item {
  display: flex;
  align-items: center;
  gap: 15px;
  font-size: 1.2rem;
  color: #ffffff;
  padding: 10px 20px;
  background-color: rgba(255, 255, 255, 0.2); /* L√†m n·ªÅn trong su·ªët cho c√°c item */
  border-radius: 8px; /* Bo tr√≤n c√°c g√≥c */
  transition: background-color 0.3s ease;
}

.contact-item a {
  text-decoration: none;
  color: #ffffff;
