/* components/styles/Header.css */
.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: #110000;
  color: white;
  padding: 20px;
  z-index: 1000;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

